<script>
  import { PUBLIC_BACKEND } from "$env/static/public"
  let rootURL = new URL(window.location)
  rootURL.pathname = ""

  function focusMe(el) {
    // I never really found out why 200ms timeout was required, but it looks
    // pretty cool, so I left it.
    setTimeout(() => el.focus(), 200)
  }
</script>

<main class="login-main">
  <form class="form-signin" method="POST" action="{PUBLIC_BACKEND}/login">
    <h3>ActLog Login</h3>
    <div class="form-floating">
      <input
        type="password"
        class="form-control"
        id="password"
        name="password"
        placeholder="Password"
        use:focusMe
      />
      <label for="password">Password</label>
      <!-- next_url is ignored in the prod server,
        but honored in the dev server-->
      <input type="hidden" name="next_url" value={rootURL.toString()} />
    </div>
    <button class="mt-2 w-100 btn btn-lg btn-primary" type="submit"
      >Login</button
    >
  </form>
</main>

<style>
  .login-main {
    display: flex;
    align-items: center;
    height: 100%;
  }
  .form-signin {
    padding: 15px;
    margin: auto;
    text-align: center;
  }
</style>
